				<button class="cancel" size="mini" @click="hidediv()">取消</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				msgType: 'success',
				value: '默认输入的内容',
				showDialog: false
			}
		},
		methods: {
			showdiv() {
				this.showDialog = true
			},
			hidediv() {
				this.showDialog = false
			}
		},
	}
</script>
